@media (min-width: 768px) {
    .contacts {
        height: 100vh;
        background-color: #ff5153;
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
        align-items: center;

    }

    .form {
        width: 80%;
        border: 1px solid;
        height: 450px;
    }

    .forTextarea {
        width: 600px;
        height: 300px;
    }

    .forInputs {
        margin: 15px;
    }
}
@media (min-width: 360px) and (max-width: 767px){
    .contacts {
        height: 100vh;
        background-color: #ff5153;
        display: flex;
        flex-direction: column;
        align-items: center;

    }

    .form {
        width: 80%;
        border: 1px solid;
        height: 450px;
    }

    .forTextarea {
        width: 300px;
        height: 300px;
    }

    .forInputs {
        margin: 15px;
    }



}